@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;1,400;1,700&display=swap');

body {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
	background-color: white;
	font-family: 'Montserrat', sans-serif;
}

.App {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 700px;
	text-align: center;
	max-width: 768px;
}

button {
	padding: 20px 60px;
	margin: 5px;
	border: none;
	background-color: #404040;
	color: aliceblue;
	font-size: 25px;
	cursor: pointer;
}

.square {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 60px;
	height: 50px;
	margin: 5px;
	background-color: #404040;
	border-radius: 8px;
	font-size: 26px;
	box-shadow: 3px 0px 33px -10px rgba(66, 68, 90, 1);
}

.square span {
	visibility: hidden;
}

.letterbox {
	display: flex;
	flex-wrap: wrap;
	border-top: 5px solid #000;
	padding: 20px;
}

input {
	height: 70px;
	width: 70px;
	margin: 20px;
	font-size: 35px;
}

.square[visible='visible'] {
	background-color: #7fadeb;
	animation: anime 1s;
}

.square[visible='visible'] span {
	visibility: visible;
}

.winText {
	position: fixed;
	height: 100px;
	top: -100px;
	right: 0;
	left: 0;
	background-color: rgb(42, 42, 42);
	color: whitesmoke;
	font-size: 40px;
	line-height: 100px;
	transition: top 1s;
}
.winText.active {
	top: 0;
}

.timer,
.points {
	position: absolute;
	top: 0;
	right: 0;
	width: 50px;
	padding: 10px;
	margin: 20px;
	background-color: white;
	font-weight: 700;
	border-radius: 8px;
	box-shadow: 3px 0px 33px -5px rgba(66, 68, 90, 1);
}

.points {
	left: 0;
}

@keyframes anime {
	0% {
		opacity: 0;
		transform: rotateY(180deg);
	}
	100% {
		opacity: 1;
		transform: rotateY(0deg);
	}
}

@media (max-width: 500px) {
	body {
		background-color: #7fadeb;
	}

	.timer,
	.points {

	}
}
